.EventsPage {
  width: 100%;
  margin: 20px;
  display: grid;
  grid-gap: 20px;
  grid-template-areas:
    "sidebar"
    "content";
}

@media (min-width: 600px) {
  .EventsPage {
    grid-template-columns: 1fr 3fr;
    grid-template-areas: "sidebar content";
  }
}


