html, body {
  height: 100%;
}

* {
  font-family: 'Montserrat', sans-serif;
}

body {
  background: url('/images/bg.jpg') no-repeat center center fixed;
  background-size: cover;
}

#root {
  height: 100vh;
}

.App .page-no-sidebar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex: 1 1;
      flex: 1 1;
}

.toast-error {
  background-color: var(--dark-red);
  color: white;
}

.dropdown-trigger:focus {
  outline: none;
}

.App .dropdown-content {
  min-width: 200px;
}

.col-section {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column nowrap;
      flex-flow: column nowrap;
  height: calc(100vh - 128px);
  background-color: var(--light-beige);
  opacity: 0.85;
  padding: 10px;
}

.section-title {
  background-color: var(--light-green);
  color: var(--light-beige);
  height: 42px;
  font-size: 15px;
  letter-spacing: 3px;
  text-align: center;
  margin: -10px -10px 0;
  padding: 10px;
}

/* cards */
.App div.card-action {
  background-color: var(--dark-gold);
}

.App .card .card-action a:not(.btn):not(.btn-large):not(.btn-small):not(.btn-large):not(.btn-floating) {
  color: white;
}




